import React, { Component } from "react";
import LogoUes from "./LogoUes";
import LogoProyeccion from "./LogoProyeccion";
import { Row, Col, Card, Tooltip, OverlayTrigger } from "react-bootstrap";

class InicioInformacion extends Component {
  render() {
    return (
      <>
        <Row className="pt-4">
          <Col align="center">
            <div className="container">
              <div className="wrap-center">
                <div className="navbar-brand">
                  <OverlayTrigger
                    placement="bottom"
                    overlay={
                      <Tooltip>
                        Clic para ir a la pgina oficial de la Universidad
                      </Tooltip>
                    }
                  >
                    <a href="https://www.ues.edu.sv/" target="_blank" rel="noreferrer">
                      <LogoUes></LogoUes>
                    </a>
                  </OverlayTrigger>
                </div>
                <div id="header_front">
                  <h1>Universidad de El Salvador</h1>
                  <h4>"Hacia la libertad por la cultura"</h4>
                </div>
              </div>
            </div>
          </Col>
          <Col align="center">
            <div className="container">
              <div className="wrap-center">
                <div className="navbar-brand">
                  <a href="http://proyeccionsocial.ues.edu.sv/" target="_blank" rel="noreferrer">
                    <LogoProyeccion></LogoProyeccion>
                  </a>
                </div>
                <div id="header_front">
                  <h1>Secretaria de proyeccin social</h1>
                  <h4>"De la mano con la comunidad"</h4>
                </div>
              </div>
            </div>
          </Col>
        </Row>
        <Row className="pt-5">
          <Card>
            <Card.Header align="center">
              Sistema informtico para el control y aplicacin del servicio
              social de los estudiantes de la Universidad de El Salvador
              (SICASS).
            </Card.Header>
            <Card.Body>
              <Card.Title>Descripcin</Card.Title>
              <Card.Text className="align-justify">
                Sistema informtico en la plataforma web, para la
                digitalizacin, automatizacin y eficiencia en la realizacin
                del proceso de registro, solicitud y control del servicio social
                que es realizado por los estudiantes de la Universidad de El
                Salvador, como prerrequisito para su titulacin.
              </Card.Text>
            </Card.Body>
          </Card>
        </Row>
      </>
    );
  }
}

export default InicioInformacion;
